<template>
  <div class="nav">
    <el-menu mode="horizontal" @select="select" :default-active="activeItem">
      <el-menu-item index="/"></el-menu-item>
      <el-menu-item index="/lists"></el-menu-item>
      <div class="seperator"></div>
      <template v-if="!username">
        <el-menu-item index="/login"></el-menu-item>
        <el-menu-item index="/register"></el-menu-item>
      </template>
      <template v-else>
        <el-menu-item index="/profile">{{ username }}</el-menu-item>
        <el-submenu index="problem-menu">
          <template slot="title"></template>
          <el-menu-item index="/problem/new"></el-menu-item>
          <el-menu-item index="/problems"></el-menu-item>
        </el-submenu>
        <el-menu-item index="/list/new"></el-menu-item>
        <el-menu-item index="/logout"></el-menu-item>
      </template>
    </el-menu>
  </div>
</template>

<script>
import { Submenu, Menu, MenuItem } from 'element-ui'

export default {
  name: 'nav-bar',
  data () {
    return {
    }
  },
  methods: {
    select (index) {
      this.$emit('routeTo', index)
    }
  },
  components: {
    ElMenu: Menu,
    ElMenuItem: MenuItem,
    ElSubmenu: Submenu
  },
  props: [
    'activeItem',
    'username'
  ]
}
</script>

<style scoped>
.seperator {
  width: 3px;
  border-radius: 3px;
  margin: 5px 20px;
  height: 50px;
  background-color: #ddd ;
  float: left;
}
</style>


